.footerContainer {
    width: 100%;
    position: fixed;
    bottom: 0;
    border-top: 1px solid black;
    padding: 15px;
    background: rgb(34, 34, 34);
}

.footerContainer p {
    font-size: 13px;
    text-align: center;
    color: #AAA;
}

.footerLinks {
    position: absolute;
    padding-right: 10px;
    top: 7.5%;
    right: 0;
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-between;
    align-items: center;
}

.invert {
    filter: invert(100%);
}

.footerLinkImage {
    padding: 8px 8px;
    max-width: 45px;
    max-height: 45px;
}

.footerLinkImage:hover {
    background-color: #cfcfcf;
}

@media (max-width: 800px) {

    .footerContainer p {
        font-size: 10px;
    }

    .footerLinks {
        margin: 0;
        padding: 0;
        position: initial;
    }

    .footerLinkImage {
        margin: 0;
        padding: 10px;
    }
}