.sourceCodeBubble {
    color: white;
    width: 100%;
}

.sourceCodeBubbleData {
    color: rgb(221, 221, 221);
    background: #272727;
    max-width: 50%;
    min-width: 50%;
    text-align: center;
    margin: 0 auto;
    padding: 20px 30px;
    border: 1px solid black;
    border-radius: 20px;
}

.sourceCodeBubbleData h3 {
    text-align: center;
    font-size: 25px;
    padding-bottom: 25px;
}

.sourceCodeCTAContainer {
    padding-bottom: 10px;
}

.sourceCodeCTA {
    background-color: #018786;
    color: white;
    font-size: 18px;
    text-decoration: none;
    padding: 15px 25px;
    margin: 15px 40px;
    border-radius: 7px;
    text-align: center;
}

.sourceCodeCTA:hover {
    cursor:pointer;
    background-color: #00a8a8;
}

@media (max-width: 800px) {
    .sourceCodeBubbleData {
        max-width: 90%;
        padding: 15px 20px;
    }
    
    .sourceCodeBubbleData h3 {
        font-size: 22px;
    }
}