.textBubble {
    color: white;
    padding-bottom: 30px;
    width: 100%;
}

.textBubbleData {
    color: rgb(221, 221, 221);
    background: #272727;
    max-width: 50%;
    min-width: 50%;
    text-align: center;
    margin: 0 auto;
    padding: 20px 30px;
    border: 1px solid black;
    border-radius: 20px;
}

.textBubbleData a {
    color: #018786;
}

.textBubbleData a:hover {
    color: #00a8a8;
}

.textBubbleData h3 {
    text-align: center;
    font-size: 25px;
    padding-bottom: 10px;
}

.textBubbleData p {
    text-align: left;
    font-size: 18px;
    white-space: pre-wrap;
}

.textBubbleData ul, .textBubbleData ol {
    padding-left: 40px;
}

.textBubbleData li {
    line-height: 25px;
}

.textBubbleVideo {
    overflow: hidden;
    padding-bottom: 56.25%;
    position: relative;
    height: 0;
}

.textBubbleVideo iframe {
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
    position: absolute;
}

.textLink {
    text-decoration: underline;
    cursor: pointer;
}

.textBold {
    font-weight: bolder;
}

.textItalic {
    font-style: italic;
}

.codeblock {
    display: block;
    border-radius: 10px;
    font-family: 'Roboto Mono', monospace;
    padding: 10px;
    background-color: #303030;
    overflow: scroll;
    overflow-y: auto;
    overflow-x: auto;
}

.codeblock-inline {
    font-family: 'Roboto Mono', monospace;
    background-color: #303030;
}

@media (max-width: 800px) {
    .textBubbleData {
        max-width: 90%;
        padding: 15px 20px;
    }
    
    .textBubbleData h3 {
        font-size: 22px;
    }

    .textBubbleData p {
        font-size: 15px;
    }
}